export default {
	properties: {},
	data: () => {
		return {

		}
	},
	attached() {},
	methods: {
		//授权成功
		bindGetUserInfo: function(res) {
			if (res.detail.userInfo) {
				wx.setStorage({
					key: 'userInfo',
					data: res.detail.userInfo,
				});
				// this.isShow = false;
				this.$emit('GetUserInfo', res.detail);
			}
		}
	}
}
